// Libraries
import React from 'react';

// Components
import Footer from './Footer';
import Header from './Header';
import Item from './Item';
import ItemFilter from './ItemFilter';
import ListItemAdd from './ListItemAdd';
import ListSettings from './ListSettings';
import ListView from './ListView';

// Stores
import ItemStore from '../stores/ItemStore';
import ListStore from '../stores/ListStore';

const ItemView = React.createClass({

    getInitialState: function() {
        return {
            items: ItemStore.getAllForCurrentList(true),
            currentFilter: ItemStore.getCurrentFilter(),
            currentList: ListStore.getCurrentList(),
            listOptions: true
        };
    },

    componentWillMount: function() {
        ItemStore.addListener("CHANGE_ITEM", this.getAllItemsFromList);
    },

    componentWillUnmount: function() {
        ItemStore.removeListener("CHANGE_ITEM", this.getAllItemsFromList);
    },

    getAllItemsFromList: function() {
        this.setState({
            items: ItemStore.getAllForCurrentList(true),
            currentFilter: ItemStore.getCurrentFilter(),
            currentList: ListStore.getCurrentList(),
            listOptions: true
        });
    },

    toggleSettings: function() {

        this.setState({
            listOptions: !this.state.listOptions
        })
    },

    render: function() {
        var listItems = this.state.items.map(function(listItem, index) {
            return (
                <Item
                    itemProps={listItem}
                    listData={this.state.items}
                    key={listItem.ID}
                />
            );
        }.bind(this))

        return (
            this.state.listOptions ?
                <div className="item-view">
                    <Header
                        navBack={true}
                        options={true}
                        title={this.state.currentList.title}
                        toggleSettings={this.toggleSettings}
                    />
                    <div className="list-item-scroll">
                        <ItemFilter filter={this.state.currentFilter} items={this.state.items} />
                        <div className="list-container">
                            {listItems}
                        </div>
                        <ListItemAdd condActions={"ItemActions"}/>
                    </div>
                    <Footer items={this.state.items} />
                </div>
            :
                <ListSettings
                    currentList={this.state.currentList}
                    toggleSettings={this.toggleSettings}
                />
        );
    }
});

export default ItemView;



